  --color-text-secondary: #a1a1aa;
  --color-text-muted: #52525b;
  --gradient-ai: linear-gradient(135deg, #00d4ff 0%, #0066ff 100%);
  --gradient-iot: linear-gradient(135deg, #00ffc8 0%, #00d4a0 100%);
  --gradient-systems: linear-gradient(135deg, #8b5cf6 0%, #6d28d9 100%);
  --gradient-hero: linear-gradient(180deg, transparent 0%, var(--color-base) 100%);
